.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar-logout{
  display: flex;
  border-bottom: 1px solid black;
}

.navbar-login{
  display: flex;
  border-bottom: 1px solid black;
}

.auth {
  background: url(https://whalebonemag.com/wp-content/uploads/2015/08/DSC_4647-Edit-1050x701.jpg);
  background-size: cover;
  height: 85vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  color: white;
}

.auth-form {
  border: 1px solid black;
  padding: 7vh 3vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(50,220,100, 0.7);
  border-radius: 19px;
}

.no-acct {
  border: 1px solid black;
  background-color: rgba(50,220,100, 0.7);
  border-radius: 5px;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  color: white;
}

.input {
  margin-bottom: 3vh;
}

.page-height {
  background: url(https://whalebonemag.com/wp-content/uploads/2015/08/DSC_4647-Edit-1050x701.jpg);
  background-size: cover;
  height: 85vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  color: white;
}

.profile-height {
  height: 85vh;
  background: url(https://whalebonemag.com/wp-content/uploads/2015/08/DSC_4647-Edit-1050x701.jpg);
  background-size: cover;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  color: white;
}

.profile-info{ 
  display: flex;
  justify-content: space-around;
}

.about-me {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 30vw;
  text-align: left;
}

.profile-numbers {
  display: flex;
  flex-direction: column;
}

.create-form {
  border: 1px solid black;
  width: 25vw;
  height: 75vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  background-color: rgba(50,220,100, 0.7);
  border-radius: 19px;
}
ul {
  list-style-type: none;
  display: flex;
}

footer {
  border-top: 1px solid black;
}

/* ----SearchBar */
.search-box {
  background: #FBFBFB;
  border: 1px solid #EAEAEA;
  border-radius: 3px;
}
 
.search-box {
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  background-color: #f2f2f2;
  background: #FBFBFB;
    border: 1px solid #EAEAEA;
    border-radius: 3px;
    height: 27px;
    margin-top: 8px;
    font-size: 14px;
  max-width: 200px;
}
 
.search-input {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  background-color: transparent;
  border: 0px;
  padding: 5px 0px;
  font-size: 1em;
  position: relative;
  outline: 0px;
  width: 75%;
}
 
.search-box {
  background: #FBFBFB;
  border: 1px solid #EAEAEA;
  border-radius: 3px;
}
.search-icon {
  float: left;
  color: #000;
  padding: 2px 10px;
